import React, { useState } from 'react';
import Navbar from 'react-bootstrap/Navbar'
import Button from 'react-bootstrap/Button'
import Nav from 'react-bootstrap/Nav'
import Modal from 'react-bootstrap/Modal'
import 'bootstrap/dist/css/bootstrap.min.css';

function Alert() {

  const [show, setShow] = useState(false);

  const handleClose = () => setShow(false);
  const handleShow = () => setShow(true);

    return (
      <div>
          <Navbar className="Navbar" bg="primary" variant="light">
          <Navbar.Brand>Koronakorrelaatio</Navbar.Brand>
        <Nav>
          <Button 
          className="nappi" 
          variant="primary"
          onClick={handleShow}
          >Info
          </Button>
        </Nav>
        </Navbar>

        <Modal 
        show={show} 
        onHide={handleClose} 
        animation={false}>
        <Modal.Header closeButton>
          <Modal.Title>Koronakorrelaatio</Modal.Title>
        </Modal.Header>
        <Modal.Body>Koronakorrelaatio nettisivu jossa voit hakea suomesta tietoa...</Modal.Body>
        <Modal.Footer>
          <Button variant="primary" onClick={handleClose}>
            Sulje
          </Button>
        </Modal.Footer>
      </Modal>
        </div>
    );
  }

export default Alert